e,\r\n    kbd,\r\n    samp {\r\n        font-family: monospace, monospace; /* 1 */\r\n        font-size: 1em; /* 2 */\r\n    }\r\n    \r\n    /**\r\n    * Add the correct font size in all browsers.\r\n    */\r\n    \r\n    small {\r\n        font-size: 80%;\r\n    }\r\n    \r\n    /**\r\n    * Prevent `sub` and `sup` elements from affecting the line height in\r\n    * all browsers.\r\n    */\r\n    \r\n    sub,\r\n    sup {\r\n        font-size: 75%;\r\n        line-height: 0;\r\n        position: relative;\r\n        vertical-align: baseline;\r\n    }\r\n    \r\n    sub {\r\n        bottom: -0.25em;\r\n    }\r\n    \r\n    sup {\r\n        top: -0.5em;\r\n    }\r\n    \r\n    /* Embedded content\r\n        ========================================================================== */\r\n    \r\n    /**\r\n    * Remove the border on images inside links in IE 10.\r\n    */\r\n    \r\n    img {\r\n        border-style: none;\r\n    }\r\n    \r\n    /* Forms\r\n        ========================================================================== */\r\n    \r\n    /**\r\n    * 1. Change the font styles in all browsers.\r\n    * 2. Remove the margin in Firefox and Safari.\r\n    */\r\n    \r\n    button,\r\n    input,\r\n    optgroup,\r\n    select,\r\n    textarea {\r\n        font-family: inherit; /* 1 */\r\n        font-size: 100%; /* 1 */\r\n        line-height: 1.15; /* 1 */\r\n        margin: 0; /* 2 */\r\n    }\r\n    \r\n    /**\r\n    * Show the overflow in IE.\r\n    * 1. Show the overflow in Edge.\r\n    */\r\n    \r\n    button,\r\n    input { /* 1 */\r\n        overflow: visible;\r\n    }\r\n    \r\n    /**\r\n    * Remove the inheritance of text transform in Edge, Firefox, and IE.\r\n    * 1. Remove the inheritance of text transform in Firefox.\r\n    */\r\n    \r\n    button,\r\n    select { /* 1 */\r\n        text-transform: none;\r\n    }\r\n    \r\n    /**\r\n    * Correct the inability to style clickable types in iOS and Safari.\r\n    */\r\n    \r\n    button,\r\n    [type=\"button\"],\r\n    [type=\"reset\"],\r\n    [type=\"submit\"] {\r\n        -webkit-appearance: button;\r\n    }\r\n    \r\n    /**\r\n    * Remove the inner border and padding in Firefox.\r\n    */\r\n    \r\n    button::-moz-focus-inner,\r\n    [type=\"button\"]::-moz-focus-inner,\r\n    [type=\"reset\"]::-moz-focus-inner,\r\n    [type=\"submit\"]::-moz-focus-inner {\r\n        border-style: none;\r\n        padding: 0;\r\n    }\r\n    \r\n    /**\r\n    * Restore the focus styles unset by the previous rule.\r\n    */\r\n    \r\n    button:-moz-focusring,\r\n    [type=\"button\"]:-moz-focusring,\r\n    [type=\"reset\"]:-moz-focusring,\r\n    [type=\"submit\"]:-moz-focusring {\r\n        outline: 1px dotted ButtonText;\r\n    }\r\n    \r\n    /**\r\n    * Correct the padding in Firefox.\r\n    */\r\n    \r\n    fieldset {\r\n        padding: 0.35em 0.75em 0.625em;\r\n    }\r\n    \r\n    /**\r\n    * 1. Correct the text wrapping in Edge and IE.\r\n    * 2. Correct the color inheritance from `fieldset` elements in IE.\r\n    * 3. Remove the padding so developers are not caught out when they zero out\r\n    *    `fieldset` elements in all browsers.\r\n    */\r\n    \r\n    legend {\r\n        box-sizing: border-box; /* 1 */\r\n        color: inherit; /* 2 */\r\n        display: table; /* 1 */\r\n        max-width: 100%; /* 1 */\r\n        padding: 0; /* 3 */\r\n        white-space: normal; /* 1 */\r\n    }\r\n    \r\n    /**\r\n    * Add the correct vertical alignment in Chrome, Firefox, and Opera.\r\n    */\r\n    \r\n    progress {\r\n        vertical-align: baseline;\r\n    }\r\n    \r\n    /**\r\n    * Remove the default vertical scrollbar in IE 10+.\r\n    */\r\n    \r\n    textarea {\r\n        overflow: auto;\r\n    }\r\n    \r\n    /**\r\n    * 1. Add the correct box sizing in IE 10.\r\n    * 2. Remove the padding in IE 10.\r\n    */\r\n    \r\n    [type=\"checkbox\"],\r\n    [type=\"radio\"] {\r\n        box-sizing: border-box; /* 1 */\r\n        padding: 0; /* 2 */\r\n    }\r\n    \r\n    /**\r\n    * Correct the cursor style of increment and decrement buttons in Chrome.\r\n    */\r\n    \r\n    [type=\"number\"]::-webkit-inner-spin-button,\r\n    [type=\"number\"]::-webkit-outer-spin-button {\r\n        height: auto;\r\n    }\r\n    \r\n    /**\r\n    * 1. Correct the odd appearance in Chrome and Safari.\r\n    * 2. Correct the outline style in Safari.\r\n    */\r\n    \r\n    [type=\"search\"] {\r\n        -webkit-appearance: textfield; /* 1 */\r\n        outline-offset: -2px; /* 2 */\r\n    }\r\n    \r\n    /**\r\n    * Remove the inner padding in Chrome and Safari on macOS.\r\n    */\r\n    \r\n    [type=\"search\"]::-webkit-search-decoration {\r\n        -webkit-appearance: none;\r\n    }\r\n    \r\n    /**\r\n    * 1. Correct the inability to style clickable types in iOS and Safari.\r\n    * 2. Change font properties to `inherit` in Safari.\r\n    */\r\n    \r\n    ::-webkit-file-upload-button {\r\n        -webkit-appearance: button; /* 1 */\r\n        font: inherit; /* 2 */\r\n    }\r\n    \r\n    /* Interactive\r\n        ========================================================================== */\r\n    \r\n    /*\r\n    * Add the correct display in Edge, IE 10+, and Firefox.\r\n    */\r\n    \r\n    details {\r\n        display: block;\r\n    }\r\n    \r\n    /*\r\n    * Add the correct display in all browsers.\r\n    */\r\n    \r\n    summary {\r\n        display: list-item;\r\n    }\r\n    \r\n    /* Misc\r\n        ========================================================================== */\r\n    \r\n    /**\r\n    * Add the correct display in IE 10+.\r\n    */\r\n    \r\n    template {\r\n        display: none;\r\n    }\r\n    \r\n    /**\r\n    * Add the correct display in IE 10.\r\n    */\r\n    \r\n    [hidden] {\r\n        display: none;\r\n    }","@import 'css/normalize';\r\n@import 'scss/var';\r\n@import 'scss/style';\r\n// @import 'sass/header';\r\n@import 'scss/footer';\r\n@import 'scss/pageup';\r\n@import 'css/brands.css';\r\n@import 'css/brands.min.css';\r\n@import 'css/fontawesome.css';\r\n@import 'css/fontawesome.min.css';\r\n@import 'css/solid.css';\r\n@import 'css/solid.min.css';\r\n@import 'css/bootstrap-reboot.min.css';\r\n@import 'css/bootstrap-grid.min.css';\r\n","\r\n// зададим общие настройки\r\nbody {\r\n    font-family: 'Roboto', sans-serif; //укажем основные шрифты\r\n    background-color: $back; // зададим цвет фона\r\n    color: $text; //укажем основной цвет текста\r\n    position: absolute;\r\n    }\r\n// h3 {\r\n// \tmargin-bottom: 100vh;\r\n// \t}\r\n// Зададим настройки для самого верхнего меню. Классы .container, .menu и его производные\r\n.container {\r\n\tmin-height: 40px; //зададим минимальную выссоту\r\n}\r\nnav {\r\n\tposition: fixed; // закрепим меню сверху\r\n\twidth: 100%; //зададим ширину\r\n\theight: 60px; //и высоту\r\n\tz-index: 2; //сделаем, чтобы меню всегда было на переднем плане\r\n\tbackground-color: $firstmenuback; //цвет фона меню\r\n\ttop: 0;\r\n}\r\n//пробежимся по пунктам в меню\r\n.menu {\r\n\tdisplay: flex; //установим резиновое отображение\r\n\tjustify-content: space-between; //выровняем объекты\r\n\tpadding: 13px 50px; //заададим отступы от текста до внутренней границы блока\r\n\tmargin: 0;\r\n\t// margin-right: 15px;\t\r\n\tlist-style-type: none; // уберём точки ненумероанного списка\r\n\t.menu_link {\r\n\t\tcolor: $firstmenutext; //зададим цвет текста\r\n\t\tfont-size: 24px; //размер шрифта\r\n\t\tfont-weight: 400; //и его жирность\r\n\t\ttext-decoration: none; //уберём подчеркивание у ссылок\r\n\t\ttext-align: center; //выровням элементы по центру\r\n\t}\r\n\t.menu_item {\r\n\t\tborder-right: 3px dotted $firstmenuborder; //сделаем разделители пунктов мню точками\r\n\t\tpadding-right:70px; //зададим внутренние отступы\r\n\t\t// ниже настроим последний элемент меню\r\n\t\t&:last-child {\r\n            border: none; //уберём для него границу, чтобы не уплывал\r\n            padding-right: 28px; //зададим внутренний отступ\r\n        }\r\n\t}\r\n}\r\n// зададим стили подзаголовка\r\n.subheader {\r\n\tmargin-top: 60px; //сзададим отступ, чтобы не прилипало к верхнему меню\r\n\tbackground-color: rgba(0,0,0, .5); //зададим цвет фона и прозрачность\r\n\tdisplay: flex; //установим резиновое отображение\r\n\tjustify-content: space-between; // организуем выравнивание элементов\r\n\r\n\t.subheader_name {\t\t\r\n\t\topacity: 0.5; //установим прозрачность\r\n\t\tcolor: #ffffff;\r\n\t\tfont-size: 28px;\r\n\t\tfont-weight: 900;\r\n\t\ttext-align: center;\r\n\t\ttext-transform: uppercase;\r\n\t\tpadding-top: 10px;\r\n\t}\r\n\t.subheader_info {\r\n\t\topacity: 0.5; //установим прозрачность\r\n\t\tcolor: #ffffff;\r\n\t\tfont-size: 26px;\r\n\t\tfont-weight: 400;\r\n\t\ttext-align: center;\r\n\t\t// text-transform: uppercase;\r\n\t\tpadding-top: 10px;\r\n\t\r\n\t}\r\n\t.img {\r\n\t\tborder-radius:  50px; //скруглим фото\r\n\t\twidth: 100px; //зададим его ширину\r\n\t\theight: 100px; //зададим высоту фото\r\n\t\tborder: 2px solid grey; //установим границу, которая будет обрамлять\r\n\t\talign-items: left;\r\n\t\tmargin: 20px 10px 10px 50px;\r\n\t\t// padding: 10px;\r\n\t}\t\r\n\t\r\n\t.subheader_call {\r\n\t\t//text-transform: uppercase;\r\n\t\tcolor: #ffffff;\r\n\t\tfont-size: 19px;\r\n\t\tfont-weight: 300;\r\n\t\ttext-align: right;\r\n\t\tmargin-right: 10px;\r\n\t\tpadding-right: 28px;\r\n\t}\r\n\t.subheader_telegram {\r\n\t\tdisplay: block;\r\n\t\tcolor: #ffffff;\r\n\t\tfont-size: 22px;\r\n\t\tfont-weight: 400;\r\n\t\ttext-align: center;\r\n\t\tline-height: 12px;\r\n\t\tmargin-right: 10px;\r\n\t\tpadding-right: 18px;\r\n\t\ttext-decoration: none;\r\n\t}\r\n\t.subheader_btn {\r\n\t\tmargin-top: 20px;\r\n\t\tmargin-left: 50px;\r\n\t\twidth: 170px;\r\n\t\theight: 40px;\r\n\t\tbox-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);\r\n\t\tborder: none;\r\n\t\tborder-radius: 7px;\r\n\t\tbackground-image: linear-gradient(to top, #a56908 0%, #d8bb8a, 80%);\r\n\t\ttext-shadow: 1px