ation: none; //уберём подчеркивание у ссылок\r\n                i {\r\n                    color: $firstmenutext;// текст элементов шрифта;\t\r\n                    font-size: 43px;\t\t\t\r\n                }\r\n                i:hover {\r\n                    color: $firstmenuback;\r\n                    background-color: $firstmenutext;\r\n                    border-radius: 100%; //скруглим\r\n    \r\n                }\t\t\r\n    }\r\n    \r\n    &__links {\r\n       // width: 390px;\r\n        display: flex;\r\n        justify-content: flex-end;\r\n        //justify-content: center;\r\n       // margin-top: 10px;\r\n        list-style-type: none;       \r\n        align-items: right;\r\n        margin-bottom: 0;\r\n        padding-left: 190px;\r\n    }\r\n    &__link {\r\n        // display: flex;\r\n        // justify-content: space-between;\r\n        width: 28%;\r\n        height: 70px;\r\n        border: 2px solid $firstmenuback;\r\n        //color: #6b6b6b;\r\n        color: $firstmenutext;\r\n        background-color: $firstmenuback;\r\n        font-size: 14px;\r\n        font-weight: 400;\r\n        text-transform: uppercase;\r\n        text-align: center;\r\n        transform: skewX(-25deg);\r\n        margin: 0 5px;\r\n        cursor: pointer;\r\n        padding: 10px;        \r\n        transform: skewX(-17deg);\r\n        transition: 0.5s all;\r\n            &:hover {\r\n                box-shadow: 3px 3px 20px $firstmenuback;\r\n                border: none;\r\n                \r\n            }\r\n\r\n            div {\r\n                transform: skewX(17deg);\r\n            }\r\n            a {\r\n                text-decoration: none; //уберём подчеркивание у ссылок\r\n                text-transform:lowercase;\r\n                font-size: 12px;\r\n                color: $firstmenutext;\r\n            }\r\n    \r\n            // &_active {\r\n            //     color: $firstmenutext;\r\n            //     background-color: $firstmenuback;\r\n            //     border: none;\r\n            // }\r\n        }\r\n    &__wrapper {\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: flex-start;\r\n        padding-top: 32px;\t\r\n        text-decoration: none; //уберём подчеркивание у ссылок\r\n    }\r\n    \r\n    &__wrapper > div {\r\n        width: 50%; }\r\n        .footer_wrapper > div:nth-child(2) {\r\n            display: flex;\r\n            justify-content: flex-end; }\r\n\r\n    &__lastline {\r\n        display: block;\r\n        //position: relative;\r\n        padding: 20px;\r\n        padding-right: 100px;\r\n        text-align: right;\r\n        font-size: 11px;\r\n        font-weight: 300;\r\n    }\r\n}",".pageup {\n  display: block;\n  position: fixed;\n  bottom: 170px;\n  right: 40px;\n  width: 40px;\n  height: 40px;\n  img {\n      width: 100%;\n  }\n}"],"sourceRoot":""}]);
